import { DAppClient, NetworkType } from '@airgap/beacon-sdk';

const dAppClient = new DAppClient({
  name: 'eatacid.xyz',
  preferredNetwork: NetworkType.GHOSTNET, // Specify the preferred network here
});

async function connectWallet() {
  try {
    const network = {
      type: NetworkType.GHOSTNET, // This remains the same, specifying Ghostnet
      rpcUrl: 'https://ghostnet.tezos.marigold.dev' // Ghostnet RPC URL
    };

    const permissions = await dAppClient.requestPermissions({ network });
    console.log('Permissions:', permissions);
  } catch (error) {
    console.error('Error connecting to wallet:', error);
  }
}

// Event listener for the connect wallet button
document.addEventListener('DOMContentLoaded', () => {
    const connectButton = document.querySelector('.button-primary.w-button');
    if (connectButton) {
        connectButton.addEventListener('click', function() {
            connectWallet();
        });
    } else {
        console.error('Connect wallet button not found');
    }
});

// Export your function if you need to use it elsewhere in your app
export { connectWallet };
